
import { ref, defineComponent } from "vue";
import { useRouter } from 'vue-router'

import * as Connection from "../src/connection";
import { Dropdownlist } from "../src/UserDataConfig/dropdownlists";

import Page from "./a/page-component.vue";
import Card from "./a/card-component.vue";
import CardBody from "./a/card-body.vue";
import Btn from "./a/btn-component.vue";
import Row from "./a/row-component.vue";
import RowCellRight from "./a/row-cell-right.vue";
import BtnHref from "./a/btn-href.vue";

export default defineComponent({
  components: {
    Page,
    Card,
    CardBody,
    Btn,
    Row,
    RowCellRight,
    BtnHref,
  },
  setup() {
    const router = useRouter()
    const dropdownlists = ref(
      Connection.loginStatus.userDataConfig.dropdownlists
    );

    const addItem = () => {
      dropdownlists.value.push(new Dropdownlist());
    };

    const spliceItem = (idx: number) => {
      dropdownlists.value.splice(idx, 1);
    };

    const doBack = () => {
      router.push(`/usercontrols`);
    };

    return { doBack, spliceItem, addItem, dropdownlists };
  },
});
